<script>
	import IntersectionObserver from "svelte-intersection-observer";
	import { fade } from "svelte/transition";

	let node, inter;

	function typewriter(node, { speed = 1 }) {
	  const valid =
	    node.childNodes.length === 1 &&
	    node.childNodes[0].nodeType === Node.TEXT_NODE;

	  if (!valid) {
	    throw new Error(
	      `This transition only works on elements with a single text node child`
	    );
	  }

	  const text = node.textContent;
	  const duration = text.length / (speed * 0.01);

	  return {
	    duration,
	    tick: t => {
	      const i = Math.trunc(text.length * t);
	      node.textContent = text.slice(0, i);
	    }
	  };
	}
</script>

<IntersectionObserver element={node} bind:intersecting={inter}>
  <div class="section" bind:this={node}>
      <div>
      {#if inter}
        <h1 transition:typewriter={{ speed: 1 }}>Early Life</h1>
      {/if}
        <img class="elimage" src="https://s3.amazonaws.com/s3.timetoast.com/public/uploads/photo/12008953/image/121889f47440ed000720d701bf18a0ea" alt="earlylife">
      {#if inter}
        <p transition:typewriter={{ speed: 200 }}>Frederick Douglass was born into slavery in or around 1818 in Talbot County, Maryland by parents Harriet Bailey and Aaron Anthony.
He separated from his mother and lived with his grandmother Betsy Bailey and his maternal grandfather Isaac, and separated from his grandparents and moved to the Wye House plantation in age 6, where Aaron Anthony worked as overseer. After Anthony died in 1826, Douglass was given to Lucretia Auld, wife of Thomas Auld, who sent him to serve Thomas' brother Hugh Auld in Baltimore.
Douglass learned alphabets from Hugh Auld's wife Sophia teaching. 
Under her husband's influence, Sophia came to believe that education and slavery were incompatible, and stopped teaching him and hid reading materials from him. However, Douglass secretly learned to read and write. 
By reading and learning, a new realm of thought led him to question and condemn the institution of slavery.
Thomas Auld took him back to Edward Coveye. He whipped Douglass so frequently as much as no time to heal himself. 
</p>
      {/if}
      </div>
    <br>
  </div>
</IntersectionObserver>

<style>
  .section {
    clear: both;
    padding-bottom: 100px;
  }
  .elimage {
    float: left;
    margin-right: 10px;
  }
</style>