<div class="section">
	<h1>Douglass' Paper</h1>
	<p>Frederick Douglass began to give lectures on behalf of the Massachusetts Anti-Slavery Society. Douglass wrote
		Narrative of the Life of Frederick Douglass to refute charges that it was
		impossible that someone could have been a slave. It is well known work is his first autobiography.
		When he returned to the United States in 1847, Douglass began publishing his own abolitionist newsletter, the North
		Star. He also became involved in the movement for women’s rights, and lectured on the subject of freedom.
		He would later include coverage of women’s rights issues in the pages of the North Star. The newsletter’s name was
		changed to Frederick Douglass’ Paper in 1851, and was published until 1860, just before the start of the Civil War.

	</p>
